.centers input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden;
}

.centers label {
    cursor: pointer;
    text-indent: -9999px;
    width: 70px;
    height: 30px;
    background: grey;
    display: block;
    border-radius: 100px;
    position: relative;
}

.centers label:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 20px;
    height: 20px;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
}

.centers input:checked+label {
    background: var(--blue);
}

.centers input:checked+label:after {
    left: calc(100% - 5px);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.centers label:active:after {
    width: 70;
}


@media screen and (max-width: 768px) {
    .centers label {
        width: 50px;
        height: 25px;
    }

    .centers label:after {
        width: 15px;
        height: 15px;
    }

    .centers label:active:after {
        width: 15px;
    }

}
